.input{
    width: 35rem;
    height: 15rem;
    background-color: #f2f2f2;
    padding: 1rem;
    display: flex;
    flex: 1;
    flex-direction: column;
    margin: auto;
    box-shadow: 0px 0px 10px 0px #f2f2f2;
    border-radius: 25px;
}

.input input{
  width: 20rem;
  padding: 12px 20px;
  margin: 8px auto;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 5px;
  box-sizing: border-box;
}

.input button{
  width: 6rem;
  color: white;
  background-color: #4CAF50;
  padding: 14px 20px;
  margin: 8px auto;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.input button:hover{
    background-color: #45a049;
}
