/* styles.css */
/* Styles for the overlay */
.image-carousel-popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.8); /* Semi-transparent black background */
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
}



.image-carousel-popup {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  max-width: 50%;
  height: 100vh; /* Set a fixed height for the container to center vertically */
  overflow: hidden; /* Hide overflow to prevent vertical scrollbars */
}

/* Styles for the "Close" button */
.close-button {
  position: absolute;
  top: 10px;
  right: 10px;
  background-color: transparent;
  border: none;
  font-size: 24px;
  cursor: pointer;
  color: #fff;
  padding: 5px;
  z-index: 9999;
  position: fixed;
}

.close-button:hover {
  color: #ff0000; /* Change color on hover */
}

/* Styles for the image */
.image-carousel-popup img {
  /* justify-content: center; */
  max-width: 100%; 
   /* max-height: 100%; */
   height: 20rem;
  margin:0 auto ;
  pointer-events: none;
  -webkit-user-select: none; /* Safari */
  -ms-user-select: none; /* IE 10 and IE 11 */
  user-select: none; /* Standard syntax */
  cursor: pointer;
  transition: transform 0.2s ease; /* Add image hover effect */
  margin:auto;
}

.image-carousel-popup img:hover {
  transform: scale(1.02); /* Zoom in on hover */
}

/* Styles for the "Previous" and "Next" buttons */
.navigation-buttons {
  position: fixed;
  top: 50%;
  transform: translateY(-50%);
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
}

.navigation-buttons button {
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  padding: 10px 20px;
}

.navigation-buttons button:hover {
  background-color: #0056b3;
}

.image-carousel-popup-overlay::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  backdrop-filter: blur(5px); /* Add a blur effect to the background */
  z-index: -1;
}

